import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';
import '../basico/imagenes.css'
import imagen from '../Utilidades/imagen.jpg';

export class Home extends Component {
  static displayName = Home.name;
  render () {
    return (
        <div>
            <Row>
                <Col xs="3">
                    <div className="pull-left image">
                        <img src={imagen} className="img-circle" alt="User Image" />
                    </div>
                </Col>
                <Col xs="9">
                    <p>
                        ALex se dedica a estudiar en la academia de programadores en la jornada de la ma&#x00f1;ana,
                        en la tarde se dedica a investigar sobre ciencia, tegnologia, historia, etc... uno de sus pasatiempos es jugar videojuegos
                        sobre estrategia (aunque es malo en ello).
                       </p>
                    <p>
                        Algunos de sus sue&#x00f1;os es poder hacer un viaje inter-estelar, o por lo menos ir al planeta marte en nuestro sistema solar.
                        Entre otros objetivos, tiene planeado crear varias StartUp a lo largo de su vida...
                      </p>
                    <p>
                        Es una persona capaz y sobresaliente, gracias a la ayuda del Proyecto GENESIS
                        obtuvo una <a href="MTA.pdf">certificacion</a> de Fundamentos de desarrollo de Software.
                      </p>
                    <p>
                        Es pasante universitario en la carrera Ingenieria en Sistemas en la Universidad Autonoma de Honduras en el valle de Sula.(UNAH-vs)
                    </p>
                </Col>
            </Row>
      </div>
    );
  }
}
